const addLineLayer  = (options) => {
    if (window.geoPolylineLayer != null) {
        window.geoPolylineLayer.delete();
        window.geoPolylineLayer = null;
        return
    }
    let url = window.document.location.href;
    let baseUrl = url.substring(0, url.lastIndexOf("/") + 1);
    var geoPolylineLayer = new window.SSmap.GeoJsonPolylineLayer();
    geoPolylineLayer.geoJson =
        baseUrl + options.url; //geojson
    geoPolylineLayer.color = window.SSmap.Color.fromRgb(...options.color); //
    geoPolylineLayer.alpha = options.alpha; //
    geoPolylineLayer.width = options.width; //  
    geoPolylineLayer.usedProperties = options.usedProperties; //(geojson, )
    geoPolylineLayer.setAltitudeMethod(
        window.SSmap.AltitudeMethod.RelativeToTerrain
    );
    geoPolylineLayer.setDistanceDisplayCondition(
        window.SSmap.Vector2.create(1.0, 1.0e7)
    ); //
    geoPolylineLayer.setAltitude(options.Altitude);
    geoPolylineLayer.setTranslucencyByDistance(
        window.SSmap.Vector4.create(200, 1.0, 1.0e9, 1.0)
    );
    geoPolylineLayer.create();
    window.geoPolylineLayer = geoPolylineLayer;
    //console.log(":",geoPolylineLayer.altitude())
}
// 
const addLabelLayer = (options) => {
    if (window.nowgeoLabelLayer) {
        window.nowgeoLabelLayer.delete();
        window.nowgeoLabelLayer = null;
        return
    }
    let url = window.document.location.href;
    let baseUrl = url.substring(0, url.lastIndexOf("/") + 1);
    var geoLabelLayer = new SSmap.GeoJsonLabelLayer();
    var vdf = new SSmap.VDistanceDisplayField(); //geojson 
    vdf.push_back({
        field: "L19",
        distanceDisplay: SSmap.Cartesian2.create(1.0, 500),
        scaleByDistance: SSmap.Cartesian4.create(100, 1.5, 2000, 0.5),
    });
    vdf.push_back({
        field: "L18",
        distanceDisplay: SSmap.Cartesian2.create(1.0, 1000),
        scaleByDistance: SSmap.Cartesian4.create(200, 1.5, 4000, 0.5),
    });
    vdf.push_back({
        field: "L17",
        distanceDisplay: SSmap.Cartesian2.create(1.0, 2000),
        scaleByDistance: SSmap.Cartesian4.create(400, 1.5, 6000, 0.5),
    });
    vdf.push_back({
        field: "L16",
        distanceDisplay: SSmap.Cartesian2.create(1.0, 3500),
        scaleByDistance: SSmap.Cartesian4.create(600, 1.5, 8000, 0.5),
    });
    vdf.push_back({
        field: "L15",
        distanceDisplay: SSmap.Cartesian2.create(1.0, 6000),
        scaleByDistance: SSmap.Cartesian4.create(800, 1.5, 10000, 0.5),
    });
    vdf.push_back({
        field: "L14",
        distanceDisplay: SSmap.Cartesian2.create(1.0, 10000),
        scaleByDistance: SSmap.Cartesian4.create(1000, 1.5, 15000, 0.5),
    });
    vdf.push_back({
        field: "L13",
        distanceDisplay: SSmap.Cartesian2.create(1.0, 20000),
        scaleByDistance: SSmap.Cartesian4.create(1200, 1.5, 20000, 0.8),
    });

    var fields = new SSmap.StringVector(); //
    fields.push_back("DLMC"); //;
``
    var categorys = new SSmap.StringVector();
    categorys.push_back("");

    geoLabelLayer.geoJson = baseUrl + options.url;

    geoLabelLayer.setNameField("ALIAS"); // geojson
    geoLabelLayer.setTerrainField("RH"); // geojson
    geoLabelLayer.setCategory(fields, categorys); //
    geoLabelLayer.setFieldsDDC(vdf); //

    geoLabelLayer.fontColor = SSmap.Color.fromRgb(...options.fontColor); //
    geoLabelLayer.fontSize = options.fontSize;
    geoLabelLayer.background = SSmap.Color.fromRgb(...options.background);
    geoLabelLayer.font = options.font;
    geoLabelLayer.create();
    return geoLabelLayer;
}
// 
const addpointLayer = (options) => {
    if (window.geoPointLayer) {
        window.geoPointLayer.delete();
        window.geoPointLayer = null;
        return
    }
    let url = window.document.location.href;
    let baseUrl = url.substring(0, url.lastIndexOf("/") + 1);
    var geoPointLayer = new window.SSmap.GeoJsonPointLayer();
    geoPointLayer.geoJson = baseUrl + options.url; //geojson
    geoPointLayer.url = baseUrl + options.Imgurl; //url
    geoPointLayer.width = options.width; //
    geoPointLayer.height = options.height; //
    geoPointLayer.scale = options.scale;
    // geoPointLayer.clustered = true; //  true:  false: ,  
    geoPointLayer.create();
    window.geoPointLayer = geoPointLayer;
}
// 
const addPolygonLayer = (options) => {
  if(window.geoPolygonLayer) {
    window.geoPolygonLayer.delete()
    window.geoPolygonLayer = null
    return
  }
  let opt = {
      url: options.url,
      color: window.SSmap.Color.fromRgb(...options.color), //
      alpha: options.alpha, //
      pickcolor: window.SSmap.Color.fromRgb(...options.pickcolor), //
      entityType: window.SSmap.EntityType.PLANE, //
      altitudetype: window.SSmap.AltitudeMethod.RelativeToTerrain, //()
      altitude: options.altitude, //1
    };
    let url = window.document.location.href;
    let baseUrl = url.substring(0, url.lastIndexOf("/") + 1);
    var geoPolygonLayer = new window.SSmap.GeoJsonPolygonLayer();
    if (opt.url) {
        geoPolygonLayer.geoJson = baseUrl + opt.url;
    }
    if (opt.string) {
        geoPolygonLayer.addString(opt.string);
    }
    geoPolygonLayer.entityType =
        opt.entityType || window.SSmap.EntityType.PLANE;
    geoPolygonLayer.fillColor =
        opt.color || window.SSmap.Color.fromRgb(0, 183, 235, 128);
    geoPolygonLayer.fillAlpha = opt.alpha || 1.0;
    geoPolygonLayer.buildingAlpha = opt.buildalpha || 1.0;
    geoPolygonLayer.pickColor =
        opt.pickcolor || window.SSmap.Color.fromRgb(255, 0, 0, 128); //
    if (opt.floorsField) {
        geoPolygonLayer.floorsField = opt.floorsField; //
    }
    // geoPolygonLayer.setAltitudeMethod(window.SSmap.AltitudeMethod.OnTerrain);
    geoPolygonLayer.granularity = 5;
    if (opt.altitudetype) {
        geoPolygonLayer.setAltitudeMethod(opt.altitudetype);
    }
    if (opt.altitude) {
        geoPolygonLayer.setAltitude(opt.altitude);
    }
    geoPolygonLayer.setDistanceDisplayCondition(
        window.SSmap.Vector2.create(1.0, 1.0e7)
    ); //
    geoPolygonLayer.create();
    window.geoPolygonLayer = geoPolygonLayer;
}
const addBillboard = (opt) => {
    let url = window.document.location.href;
    let baseUrl = url.substring(0, url.lastIndexOf("/") + 1);
    let bbEntity = new SSmap.BillboardEntity();
    bbEntity.position = opt.position; //
    bbEntity.scale = opt.scale == undefined ? 1.0 : opt.scale; //
    if (opt.url) {
        bbEntity.url = baseUrl + opt.url; //
    }

    if (opt.imageWidth) {
        bbEntity.imageWidth = opt.imageWidth; //
    }
    if (opt.imageHeight) {
        bbEntity.imageHeight = opt.imageHeight; //
    }
    //
    if (opt.altitude) {
        bbEntity.setAltitude(opt.altitude); //
    }
    //
    if (opt.altitudeMethod || 'Absolute') {
        opt.altitudeMethod = 'Absolute'
        bbEntity.setAltitudeMethod(opt.altitudeMethod);
    }
    // value
    // bbEntity.addProperty("name", "");
    // bbEntity.addProperty("pos", "");

    bbEntity.setCollection(SSmap.BillboardCollection.Instance()); //Collection
    bbEntity.name = opt.name || "Billboardpoint";
    return bbEntity;
}
export const SSViewer = {
  scene: {
    primitives: {
      // 
      add: (tileset) => {
        if(window.localStorage.getItem('engine') === 'SSEngine') {
            var entity = new window.SSmap.Entity();
            entity.addComponent(tileset);
            window.viewer.scene.addEntity(entity);
        }else {
            window.viewer.scene.primitives.add(tileset)
        }
        return
        switch (options.type) {
          case 'tileset':
            var entity = new window.SSmap.Entity();
            entity.addComponent(options.tileset);
            window.GlobalViewer.scene.addEntity(entity);
            break;
          case 'labelLayer':
            options.labelLayer.create()
            break;
          case 'polylineLayer':
            options.polylineLayer.create()
            break;
          case 'geoPointLayer':
            options.geoPointLayer.create()
            break;
          case 'geoPolygonLayer':
            options.geoPolygonLayer.create()
            break;
          default:
            break;
        }
      },
      // 
      remove: (tileset) => {
        tileset = tileset || window.tileset
        if(window.localStorage.getItem('engine') === 'SSEngine') {
             tileset.entity && tileset.entity.delete()
        }else {
            window.viewer.scene.primitives.remove(tileset)
        }
        return
        switch (options.type) {
          case 'tileset':
            window.tileset.entity && window.tileset.entity.delete()
            break;
          case 'geoPointLayer':
            try {
              window.geoPointLayer.delete();
              window.geoPointLayer = null;
            } catch (error) {
              console.log(error);
            }
            break;
          case 'geoPolygonLayer':
            try {
              window.geoPolygonLayer.delete();
              window.geoPolygonLayer = null;
            } catch (error) {
              console.log(error);
            }
            break;
            break;
          default:
            try {
              options.layer.delete();
              window[options.layer] = null;
            } catch (error) {
              console.log(error);
            }
            break;
        }
      }
    },
    globe: {
      // 
      ImageryLayer: {
        destroy: (nowLayer = window.nowLayer) => {
          if(window.nowLayer) {
            try { // webremoveImageryLayer
              window.GlobalViewer.scene.globe.removeImageryLayer(nowLayer);
            } catch (error) {
                console.log(error);      
            }
            window.nowLayer.delete();//delete
            delete window.nowLayer;
            window.nowLayer = null;
          }
        }
      },
      pick: (ray,scene) => {
        if(window.localStorage.getItem('engine') === 'SSEngine') {
          scene = new SSmap.RaycastHit(); //
          //
          var rayok = window.viewer.scene.raycast(ray, scene); //
          var point = 0;
          if (rayok) {
              if (scene) {
                  //
                  point = scene.point; //Vector3
              }
          }
          scene.delete();
          return point
        }else {
          return window.viewer.scene.globe.pick(ray, scene);
        }
      }
    },
    pickPosition: (position) => {
      if(window.localStorage.getItem('engine') === 'SSEngine') {
        var camera = window.viewer.scene.mainCamera;
        var hit = new SSmap.RaycastHit(); //
        //
        var ray = camera.screenPointToRay(position.x, position.y);
        var rayok = window.viewer.scene.raycast(ray, hit); //
        var point = 0;
        var tohic = null;
        if (rayok) {
            if (hit) {
                //
                point = hit.point; //Vector3
                //Vector3
                tohic = point.toCartesian3().toCartographic();
            }
        }
        hit.delete();
        return point
      }else {
        return window.viewer.scene.pickPosition(position)
      }
    },
    camera: {
      pickEllipsoid: (position) => {
        return window.viewer.scene.camera.pickEllipsoid(position)
      }
    }
  },
  camera: {
    // 
    flyTo: ({destination, duration=3, heading=0, pitch=-90, roll=0}) => {
        if(window.localStorage.getItem('engine') === 'SSEngine') {
          const position = window.SSmap.Cartographic.fromDegrees(
            ...destination
          ).toVector3();
          window.viewer.scene.mainCamera.cameraController().flyTo(position, duration, heading, pitch, roll)
        }else {
          window.viewer.camera.flyTo({
              destination : Cartesian3.fromDegrees(...destination),
              orientation : {
                  heading : Math.toRadians(heading),
                  pitch : Math.toRadians(pitch),
                  roll : Math.toRadians(roll)
              }
          });
        }
    },
    getPickRay: (position) => {
        if(window.localStorage.getItem('engine') === 'SSEngine') {
          return window.viewer.scene.mainCamera.screenPointToRay(position.x, position.y)
        }else {
          return window.viewer.camera.getPickRay(position);
        }
    },
  },
  // EntityCollection
  entities: {
    add(options) {
      if(!defined(options)) {
        throw new Error(`options is required.`);
      }
      // 
      if(window.localStorage.getItem('engine') === 'SSEngine') {
          let returnObj
          const classType = ['labelLayer','polygonLayer','polylineLayer','pointLayer','point']
          classType.find(it => {
            if(Object.keys(options).includes(it)) {
              if(!defined(options[it])) {
                throw new DeveloperError(`${it} is an empty object.`);
              } 
              switch (it) {
                case 'labelLayer':
                  addLabelLayer(options[it])
                  break;
                case 'polylineLayer':
                  addLineLayer(options[it])
                  break;
                case 'pointLayer':
                  addpointLayer(options[it])
                  break;
                case 'polygonLayer':
                  addPolygonLayer(options[it])
                case 'point':
                  returnObj =  addBillboard({...options[it],position: options.position})
                  break;
                default:
                  break;
              }
            }
          })
          return returnObj
      }else {
        return window.viewer.entities.add({
          ...options
        })
      }
    },
    remove(removeObj) {
      if(window.localStorage.getItem('engine') === 'SSEngine') {
          removeObj && removeObj.delete()
      }else {
        window.viewer.entities.remove(removeObj)
      }
    }
  },
  imageryLayers: {
    addImageryProvider: (layer) => {
        layer = layer || window.nowLayer 
        if(window.localStorage.getItem('engine') === 'SSEngine') {
            window.viewer.scene.globe.addImageryLayer(window.nowLayer);
        }else {
            window.viewer.imageryLayers.addImageryProvider(layer)
        }
    },
    add: (layer) => {
       layer = layer || window.nowLayer 
        if(window.localStorage.getItem('engine') === 'SSEngine') {
            layer.componentComplete && layer.componentComplete();
        }else {
            window.viewer.imageryLayers.add(layer)
        }
    },
    remove: (layer) => {
      layer = layer || window.nowLayer 
      if(window.localStorage.getItem('engine') === 'SSEngine') {
          try { // webremoveImageryLayer
              window.viewer.scene.globe.removeImageryLayer(layer);
            } catch (error) {
                console.log(error);      
            }
             window.nowLayer && window.nowLayer.delete();//delete
            delete window.nowLayer;
            window.nowLayer = null;
      }else {
          // window.viewer.imageryLayers.remove(layer) // 
          // 
          // console.log(window.viewer.imageryLayers);
          // window.viewer.imageryLayers.remove(layer)
          // window.nowLayer = null
          // return
          if(!layer) return
          const imageryLayers = window.viewer.imageryLayers
          for (var i = 0; i < imageryLayers.length; i++) {
              var targetLayers = imageryLayers.get(i);
              if (targetLayers.imageryProvider === layer.imageryProvider) {
                  // 
                  imageryLayers.remove(targetLayers);
                  break;
              }
          }
          window.nowLayer = null
      }
    }
  },
  // 
  dataSources: {
    remove: (layer) => {
      layer = layer || window.nowgeoLabelLayer 
      if(window.localStorage.getItem('engine') === 'SSEngine') {
          try { // webremoveImageryLayer
              layer.delete();
              window.nowgeoLabelLayer = null;
            } catch (error) {
                console.log(error);      
            }
      }else {
          // window.viewer.imageryLayers.remove(layer) // 
          // 
          layer && window.viewer.dataSources.remove(layer,true);
      }
    }
  },
  // 
  screenSpaceEventHandler: {
    action: null,
    setInputAction: function(action,type,modifier) {
      if(window.localStorage.getItem('engine') === 'SSEngine') {
          if (!defined(action)) {
            throw new Error("action is required.");
          }
          if (!defined(type)) {
            throw new Error("type is required.");
          }
          if(type === 'RIGHT_CLICK') return
          this.action = action
          document.getElementById("cesium-viewer").addEventListener("mousedown", (event) => this.mouseClickEvent(event))
      }else {
        const clickAction = window.viewer.screenSpaceEventHandler.setInputAction(action,type,modifier)
        return clickAction
      }
    },
    removeInputAction: function(action) {
      if(window.localStorage.getItem('engine') === 'SSEngine') {
          document.getElementById("cesium-viewer").removeEventListener("mousedown",this.mouseClickEvent)
      }else {
        return window.viewer.screenSpaceEventHandler.removeInputAction(action)
      }
    },
    mouseClickEvent: function(event) {
      if (event.button == 0) {
        this.action && this.action({position:{x:event.x,y:event.y}})
      } else if (event.button == 2) {
        this.removeInputAction()
        this.action = null
      }
    }
  },
} 
